export default {
  name: 'FilterMenu',
  props: ['filter', 'show'],
  data() {
    return {
      selectedItem: 'all'
    }
  },
  methods: {
    handleClick(item) {
      if (!item.name) return
      this.selectedItem = item.id
      this.$emit('filterMenuClick', {name: this.filter.name, item})
    },
  },
}
